@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;1,300;1,400&display=swap");
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --color-primary: #eb2f64;
    --color-primary-light: #ff3366;
    --color-primary-dark: #BA265D;
    --color-grey-light-1: #faf9f9;
    --color-grey-light-2: #f4f2f2;
    --color-grey-light-3: #f0eeeeee;
    --color-grey-light-4: #ccc;
    --color-grey-dark-1: #333;
    --color-grey-dark-2: #777;
    --color-grey-dark-3: #999;
    --shadow-dark: 0 1rem 4rem rgba(0, 0, 0, 0.3);
    --shadow-light: 0 1rem 4rem rgba(0, 0, 0, 0.25);
    --line: 1px solid var(--color-grey-light-2);
}

$bp-largest:75em; //1200px
$bp-large:68.75em; //1100px
$bp-medium:56.25em; //900px
$bp-small:37.5em; //600px
$bp-smallest:31.25em; //500px
*,
*::after,
*::before {
    box-sizing: initial;
}

html {
    box-sizing: border-box;
    font-size: 62.5%; //1rem = 10px
    @media only screen and (max-width: $bp-large) {
        font-size: 50%;
    }
}

body {
    font-family: "Open sanse", sans-serif;
    font-weight: 400;
    line-height: 1.6;
    color: var(--color-grey-dark-2);
    background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
    background-size: cover;
    background-repeat: no-repeat;
}